import api from 'api';

export const getGeneralContent = () => {
    return api.get('/', {
        params: {
            sheet: 'generalContent'
        }
    }).then(response => {
        return response.data.reduce((acc, val) => {
            acc[val.contentType] = acc[val.contentType] || [];
            acc[val.contentType].push(val.content);
            return acc;
        }, {});
    });
};

export const getJobTypes = () => {
    return api.get('/', {
        params: {
            sheet: 'jobTypes'
        }
    }).then(response => {
        return response.data.reduce((acc, val) => {
            acc[val.sheet] = val.jobType;
            return acc;
        }, {});
    });
};

export const getJobData = (jobSheet) => {
    return api.get('/', {
        params: {
            sheet: jobSheet
        }
    }).then(response => {
        return response.data.reduce((acc, val) => {
            acc[val.itemType] = acc[val.itemType] || [];
            acc[val.itemType].push(val.content);
            return acc;
        }, {});
    });
};

export default {
    getGeneralContent,
    getJobTypes,
    getJobData,
};