<template>
  <div class="col s12 m7 template">
    <div class="card-panel grey lighten-5">
      <content-placeholders v-if="!displayJobDetails" :animated="loading" style="width:300px">
        <content-placeholders-heading :img="false"/>
        <content-placeholders-text :lines="21"/>
      </content-placeholders>

      <span v-if="displayJobDetails" class="black-text generatedContent">
        <h5>{{ jobDetails.jobNameVariation }}</h5>

        <span
          v-if="jobDetails.salaryRangeFrom && jobDetails.salaryRangeTo"
          style="font-size:13px"
        >{{ jobDetails.salaryRangeFrom }} - {{ jobDetails.salaryRangeTo }}</span>

        <p>{{ jobDetails.introParagraph }}</p>

        <p v-if="jobDetails.companyDescription">{{ jobDetails.companyDescription }}</p>

        <h6>{{ jobDetails.jobDetailsHeading }}</h6>
        <p>{{ jobDetails.detailsParagraph1 }}</p>
        <p>{{ jobDetails.detailsParagraph2 }}</p>
        <p v-if="showParagraph()">{{ jobDetails.detailsParagraph3 }}</p>

        <h6>{{ jobDetails.expectationsHeading }}</h6>
        <p>{{ jobDetails.expectationsParagraph }}</p>

        <random-list type="expectationsBullet" :data="jobDetails"/>

        <h6>{{ jobDetails.benefitsHeading }}</h6>
        <p>{{ jobDetails.benefitsParagraph }}</p>

        <random-list type="benefitsBullet" :data="jobDetails"/>

        <h6>{{ jobDetails.applicationHeading }}</h6>
        <p>{{ jobDetails.applicationParagraph }}</p>
        <p>{{ jobDetails.instructionsImageURL }}</p>
      </span>
    </div>
  </div>
</template>

<script>
import { Random } from 'random-js';
import RandomList from './RandomList';

export default {
    name: 'Template',
    components: {
        RandomList
    },

    data() {
        return {
            displayJobDetails: false,
            loading: false,
            jobDetails: {}
        };
    },

    mounted() {
        let elems = document.querySelectorAll('select');
        M.FormSelect.init(elems);
    },

    created() {
        this.$root.$on('generatingContent', () => {
            this.displayJobDetails = false;
            this.loading = true;
        });

        this.$root.$on('showJobDetails', data => {
            this.loading = false;

            if (data) {
                this.displayJobDetails = true;
                this.jobDetails = data;
            } else {
                this.displayJobDetails = false;
            }
        });
    },

    methods: {
        showParagraph() {
            const random = new Random();
            return random.bool();
        }
    }
};
</script>

<style scoped>
.template {
    margin-top: 0.6rem;
}
</style>
