import { Random } from 'random-js';

export const getRandomData = (inputObject) => {
    const random = new Random();
    let data = {};

    for (const key in inputObject) {
        if (inputObject.hasOwnProperty(key)) {
            const elements = inputObject[key];
            data[key] = random.pick(elements);
        }
    }

    return data;
};

export const resolveTemplates = (data) => {
    for (const key in data) {
        if (data.hasOwnProperty(key)) {
            let string = data[key];

            let template = string.match(/{{\s*[\w\.]+\s*}}/g) || [];
            let resolutions = template.map(x => {
                let match = x.match(/[\w\.]+/);

                return {
                    key: match[0],
                    placeholder: match.input
                }
            });

            resolutions.forEach(resolution => {
                if (data[resolution.key]) {
                    data[key] = data[key].replace(new RegExp(resolution.placeholder, 'g'), data[resolution.key]);
                } else {
                    throw new Error(`Value for key "${resolution.key}" is missing!, Content key "${key}"`);
                }
            });
        }
    }
};

export default {
    getRandomData,
    resolveTemplates,
};