<template>
  <ul class="list-styled">
    <li v-for="value in listItems" :key="value">
      {{ value }}
    </li>
  </ul>
</template>

<script>
import { Random } from 'random-js';

export default {
    name: 'RandomList',
    props: {
        type: {
            type: String,
            required: true
        },
        data: {
            type: Object,
            required: true
        },
        min: {
            type: Number,
            default: 5
        },
        max: {
            type: Number,
            default: 10
        }
    },
    computed: {
        listItems() {
            const random = new Random();

            let list = Object.keys(this.data)
                .filter(key => key.includes(this.type))
                .map(key => this.data[key]);

            random.shuffle(list);

            if (list.length <= 5) {
                return list;
            }

            return random.sample(list, random.integer(this.min, this.max));
        }
    }
};
</script>

<style scoped>
ul.list-styled li {
    margin-left: 1.5rem;
    list-style: disc;
}
</style>
