<template>
  <div class="col s12 m5 input-form">
    <div class="card-panel grey lighten-5">
      <span class="black-text">
        <div class="row" :class="{'disable-form': loading}">
          <form class="col s12">
            <div class="row">
              <div class="input-field col s12">
                <input
                  id="company_name"
                  v-model="companyName"
                  type="text"
                  class="validate"
                  :disabled="loading"
                >
                <label for="company_name">Company Name</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <textarea
                  id="company_description"
                  v-model="companyDescription"
                  class="materialize-textarea"
                  :disabled="loading"
                />
                <label for="company_description">Company Description (optional)</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <select v-model="jobType" @change="fetchJobData">
                  <option v-for="(value, key) in jobTypes" :key="key" :value="key">{{ value }}</option>
                </select>
                <label>Job Type</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <select v-model="jobPostSite">
                  <option v-for="value in jobPostWebsites" :key="value" :value="value">{{ value }}</option>
                </select>
                <label>Job Post Website</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input
                  id="instructions_image"
                  v-model="instructionsImageURL"
                  type="text"
                  class="validate"
                  :disabled="loading"
                >
                <label for="instructions_image">Instructions Image Url</label>
              </div>
            </div>
          </form>
        </div>
      </span>
      <div class="row">
        <div class="input-field col s6">
          <a
            class="waves-effect waves-light btn"
            :class="{disabled:loading}"
            @click="generateContent"
          >Generate Content</a>
        </div>
        <div class="input-field col s6">
          <a
            v-if="!loading && canCopy"
            class="waves-effect waves-light btn deep-orange lighten-1"
            @click="copyContent"
          >Copy Content</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import service from 'service';
import helper from 'helper';
import { setTimeout } from 'timers';

export default {
    name: 'Form',
    data() {
        return {
            // view data
            jobTypes: {},
            jobPostWebsites: [],

            //model
            companyName: '',
            companyDescription: '',
            jobType: '',
            jobPostSite: '',
            instructionsImageURL: '',

            jobDataCache: {},
            selectedJobData: {},
            generalContent: {},

            // ui
            loading: false,
            canCopy: false
        };
    },
    updated() {
        this.initSelectFields();
    },
    async mounted() {
        this.initSelectFields();

        this.generalContent = await service.getGeneralContent();
        this.jobTypes = await service.getJobTypes();
        this.jobPostWebsites = this.generalContent.jobPostSite;

        this.jobPostSite =
            this.jobPostWebsites.length && this.jobPostWebsites[0];

        this.loading = false;
    },

    created() {
        this.loading = true;
    },

    methods: {
        generateContent() {
            this.$root.$emit('generatingContent');
            let generalContent = helper.getRandomData(this.generalContent);
            let jobInfo = helper.getRandomData(this.selectedJobData);
            let data = {
                ...generalContent,
                ...jobInfo,
                jobPostSite: this.jobPostSite,
                companyName: this.companyName,
                companyDescription: this.companyDescription,
                instructionsImageURL: this.instructionsImageURL
            };

            try {
                helper.resolveTemplates(data);
                this.canCopy = true;
            } catch (error) {
                this.canCopy = false;
                data = null;
                M.toast({
                    displayLength: 8000,
                    html: error.message
                });
            }

            this.$root.$emit('showJobDetails', data);
        },

        copyContent() {
            let content = document.getElementsByClassName('generatedContent')[0]
                .innerHTML;

            let listener = e => {
                e.clipboardData.setData('text/html', content);
                e.clipboardData.setData('text/plain', content);
                e.preventDefault();
            };
            document.addEventListener('copy', listener);
            document.execCommand('copy');
            document.removeEventListener('copy', listener);
        },

        async fetchJobData() {
            if (this.jobDataCache[this.jobType]) {
                this.selectedJobData = this.jobDataCache[this.jobType];
                return;
            }

            this.loading = true;
            this.jobDataCache[this.jobType] = await service.getJobData(
                this.jobType
            );
            this.selectedJobData = this.jobDataCache[this.jobType];
            this.loading = false;
        },

        initSelectFields() {
            let elems = document.querySelectorAll('select');
            M.FormSelect.init(elems);
        }
    }
};
</script>

<style scoped>
.input-form {
    margin-top: 0.6rem;
}

.disable-form {
    pointer-events: none;
    opacity: 0.4;
}
</style>
